import React from "react"
import { css } from "@emotion/react"
import { Link, graphql } from "gatsby"
import { rhythm } from "../utils/typography"
import Layout from "../components/layout"
import Img from "gatsby-image"

export default function Home({ data }) {
  return (
    <Layout>
      <div>
        <h1 id="about-me">
          About Me
        </h1>
        
        <p>
        I am a Taiwanese-American <strong>Structural Engineer</strong> who graduated from UC San Diego in 2020 and I am currently working at <strong>General Atomics</strong> as an Associate Stress Engineer. I have been the project manager and airframe lead of <strong>Triton UAS</strong> (formerly UCSD AUVSI), a student fellow in the <strong>20th Los Alamos Dynamics Summer School</strong>, and a undergraduate researcher in <strong>Professor Hyonny Kim’s composite lab</strong>.
        </p>

        <p css={css`text-align: center`}><Img 
          fluid={data.swallowTestFlight.childImageSharp.fluid}
          css={css`display:block; margin:auto`}
        /> On a test flight with the Swallow at Riverside RC field, California</p>

        <h1 id="projects">Projects</h1>
        <h3>Triton UAS (formerly UCSD AUVSI)</h3>
        <p> <strong><a 
              href="https://tuas.ucsd.edu/"
              target="blank"
            >
                Triton UAS
            </a></strong> is an multidisciplinary, student-led project team that designs, builds, and tests a composite, fixed-wing airplane to compete against over 75 teams from around the world in the annual <strong><a
              href="https://www.auvsi-suas.org/"
              target="blank"
            >
              AUVSI Student UAS Competition
            </a></strong>. <br></br><br></br>
        I was part of the team for all four years of my undergrad and was an airframe lead for three years and the project manager for two. <br></br><br></br>
        As the lead of the airframe team, I built two 11 ft wingspan composite airframes from scratch, reinforced three off-the-shelf UAVs with composites, and initiated the design of a new composite UAV in response to updated competition requirements. <br></br><br></br>
        As a project manager, I worked on both the business side of the team as well as system integration of the airframe, embedded, and software systems. On the business side of the team, I created a business team, presented at various outreach events, and secured funding from alumni, companies, and university donations and funding sources. On the integration side, I led the integration and planning for test flights throughout the school year to prepare for competition. <br></br><br></br>
        During my time in Triton UAS, our team placed as high as top 10 out of over 50 teams from around the world. The systems of our custom-built airframe, the Fiber One, are described in our <strong><a 
          href="https://drive.google.com/file/d/1S_AH4lyAObc_71IEXdjfUZINnVlGDrzD/view?usp=sharing"
          target="blank"
        >
          technical paper
        </a></strong> and <strong><a 
          href="https://youtu.be/fsosQoJAD-k"
          target="blank"
        >
          flight readiness review video
        </a></strong>.
        </p>

        <p css={css`text-align: center`}><Img 
          fluid={data.auvsiComp.childImageSharp.fluid}
          css={css`display:block; margin:auto`}
        /> Competition team in 2017 at Patuxent Naval Air Station, Maryland</p>

        <h3>Los Alamos Dynamics Summer School</h3>
        <p>The <strong><a
            href="https://www.lanl.gov/projects/national-security-education-center/engineering/los-alamos-dynamic-summer-school/"
            target="blank"
          >
            Los Alamos Dynamics Summer School
          </a></strong> is a program by Los Alamos National Laboratory (LANL) in Los Alamos, New Mexico. <br></br><br></br>
        As part of the <strong><a
            href="https://www.lanl.gov/projects/national-security-education-center/engineering/los-alamos-dynamic-summer-school/programs-2019.php"
            target="blank"
          >
          20th cohort
          </a></strong>, I worked with two other students and under the guidance of three mentors to go through the research from start to finish within 10 weeks. Our team performed a literature review, wrote a time synchronization algorithm, designed and conducted a modal analysis experiment to benchmark the algorithm, published a <strong><a
            href="https://doi.org/10.1007/978-3-030-47709-7_17"
            target="blank"
          >
            conference paper
            </a></strong>, submitted a patent for the algorithm, and presented at the <strong><a
              href="https://sem.org/imac/"
              target="blank"
            >  
              International Modal Analysis Conference.</a></strong> <br></br><br></br>
        In addition to performing research, we worked alongside and interacted with the 21 students in our cohort from over the US, toured the state-of-the-art equipment and facilities at LANL, and learned about cutting-edge research from seminars hosted by researchers at LANL. <br></br><br></br>
        </p>
        
        <p css={css`text-align: center`}><Img 
          fluid={data.losAlamos.childImageSharp.fluid}
          css={css`display:block; margin:auto`}
        /> Members and mentors of the LADSS program ready to summit Wheeler Peak in New Mexico</p>

        <h3>Professor Hyonny Kim's Composites Lab</h3>
        <p> I worked as an undergraduate research assistant in Professor Kim's composites lab helping manufacture composite fuselage panels for impact tests. The project was focused on detecting damage from accidental ground vehicle impact to composite commercial airplanes that might not be visibly observable from the exterior. I learned how to use Shapkeo CNC and HSMWorks to machine parts for the fuselage panels and participated in the fuselage panel testing.
        </p>

        <p css={css`text-align: center`}><Img 
          fluid={data.kimLab.childImageSharp.fluid}
          css={css`display:block; margin:auto`}
        /> Test setup for impact testing on composite fuselage panel in Powell Labs @ UC San Diego</p>

        <h1 id="personal">Personal</h1>
        <p>In my free time, I enjoy rock climbing at Mesa Rim and hiking. When I have the opportunity (and money), I enjoy traveling and experiencing living in new places. So far, I've traveled to New Mexico, Taiwan, and Italy. I also enjoy listening to podcasts (Nightvale, Dear Hank and John, and a healthy dose of NPR) and watching YouTube (vlogbrothers, This Old Tony, Lizzie Bennet Diaries, and many more). Finally, I enjoy both eating out and cooking at home. Some of my favorite vegetarian places in San Diego include An's Dry Cleaning (gelato), Blind Lady's Ale House (BLAH), and Mendocino Farms. <br></br><br></br>
        </p>

        <p css={css`text-align: center`}><Img 
          fluid={data.revelleInRome.childImageSharp.fluid}
          css={css`display:block; margin:auto`}
        /> Group photo with the UC San Diego Revelle in Rome study abroad program</p>
        
        <h1 id="blog">Blog</h1>
        <h4>{data.allMarkdownRemark.totalCount} Posts</h4>
        {data.allMarkdownRemark.edges.map(({ node }) => (
          <div key={node.id}>
            <Link
              to={node.fields.slug}
              css={css`
                text-decoration: none;
                color: inherit;
              `}
            >
              <h3
                css={css`
                  margin-bottom: ${rhythm(1 / 4)};
                `}
              >
                {node.frontmatter.title}{" "}
                <span
                  css={css`
                    color: #555;
                  `}
                >
                  — {node.frontmatter.date}
                </span>
              </h3>
              <p>{node.excerpt}</p>
            </Link>
          </div>
        ))}

        <h1 id="contact">Contact</h1>
        <a 
          href="https://docs.google.com/document/d/1bdYz95cJaUYfIVvLNPmyhi3L0qGssA_JViwOm85SgJk/edit?usp=sharing"
          target="blank"
        >
          cv
        </a>
        <br></br>
        <a
          href="mailto:jessica.y.chan.5@gmail.com"
        >
          email
        </a>
        <br></br>
        <a
          href="https://github.com/jessica-chan-5/"
          target="blank"
        >
          linkedin
        </a>
        <br></br>
        <a
          href="https://drive.google.com/drive/folders/1u7YFWjbiB87x8mYiWOhyRkG1Vq14q-4z?usp=sharing"
          target="blank"
        >
          publications
        </a>
      </div>
    </Layout>
  )
}

export const image400px = graphql`
  fragment image400px on File {
    childImageSharp {
      fluid(maxWidth: 400, quality: 100) {
        ...GatsbyImageSharpFluid
        ...GatsbyImageSharpFluidLimitPresentationSize
      }
    }
  }
`
export const query = graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          fields {
            slug
          }
          excerpt
        }
      }
    }

    auvsiComp: file(relativePath: { eq: "images/auvsi_competition_2017.jpg" }) {
      ...image400px
    }
    kimLab: file(relativePath: { eq: "images/hyonny_kim_lab.jpg" }) {
      ...image400px
    }
    icon: file(relativePath: { eq: "images/icon.png" }) {
      ...image400px
    }
    losAlamos: file(relativePath: { eq: "images/ladss.jpg" }) {
      ...image400px
    }
    swallowTestFlight: file(relativePath: { eq: "images/picture_with_swallow.jpg" }) {
      ...image400px
    }  
    revelleInRome: file(relativePath: { eq: "images/revelle_in_rome.jpg" }) {
      ...image400px
    }   
  }
`